import { createAsyncThunk, createSlice } from "@reduxjs/toolkit";
import { areAllCharsInStringAInStringB, countSpaces, delay, removeVietnameseTones } from "../general/Utilities";
import { words, words2, wordsUnsign, wordsUnsign2 } from "../parseData/parseDataResult";

export const inputKey = 'input-key';

export const hackLiXiSlice = createSlice({
    name: 'hackLiXi',
    initialState: {
        isFindExactly: true,
        isUnsign: false,
        result: [] as string[],
        words: [] as string[],
        minWordLength: 2,
    },
    reducers: {
        resetResult: (state) => {
            state.result = [];
        },
        addResult: (state, action) => {
            state.result = [...state.result, action.payload];
        },
        setWords: (state, action) => {
            state.words = action.payload;
        },
        setIsFindExactly: (state, action) => {
            state.isFindExactly = action.payload;
        },
        setIsUnsign: (state, action) => {
            state.isUnsign = action.payload;
        },
        setMinWordLength: (state, action) => {
            state.minWordLength = action.payload;
        }
    }
})


export const calculateInputInfo = {
    isContinue: true,
};
export const calculateInput = createAsyncThunk('calculateInput', async ({ input: _input, isFindExactly, isUnsign, callback, minWordLength }: { input: string, isFindExactly: boolean, isUnsign: boolean, callback?: () => void, minWordLength: number }, { dispatch }) => {
    calculateInputInfo.isContinue = true;
    localStorage.setItem(inputKey, _input);
    dispatch(hackLiXiSlice.actions.resetResult());
    await delay(20);
    const myWords = isUnsign ? wordsUnsign : words;
    const myWords2 = isUnsign ? wordsUnsign2 : words2;

    const input = (isUnsign ? removeVietnameseTones(_input) : _input).toLowerCase();
    // console.log('input', input);
    const arrWord = myWords.filter(word => { return areAllCharsInStringAInStringB(word, input); })
        .filter(word => { return word.length >= minWordLength; })
        ;
    console.log('arrWord', arrWord);
    dispatch(hackLiXiSlice.actions.setWords(arrWord));
    await delay(20);
    const length = countSpaces(input) + 1;
    console.log('length', length);

    const calc = async (current: string, arr: string[], currentIndex: number, length: number) => {
        // console.log('current', current);

        if (!calculateInputInfo.isContinue) return;

        if (currentIndex >= length) {
            if (current.length === input.length) {
                dispatch(hackLiXiSlice.actions.addResult(current));
                await delay(1);
            }
        }
        else {
            for (let i = 0; i < arr.length; i++) {
                const item = arr[i];
                const newCurrent = current + (current.length === 0 ? '' : ' ') + item;
                // console.log('newCurrent', newCurrent);
                const newIndex = currentIndex + 1;

                const isValid = isFindExactly ?
                    (
                        newCurrent.length <= input.length
                        //newcurrent nếu ít hơn 2 chữ thì => true, nếu >= 2 chữ mới check
                        // && newCurrent.split(' ').length < 2 ? true : myWords2.includes(newCurrent.split(' ').slice(-2).join(' '))
                        && checkExactly(newCurrent, myWords2)
                        && areAllCharsInStringAInStringB(newCurrent, input)
                    )
                    : (
                        newCurrent.length <= input.length
                        // && words2.includes(newCurrent.split(' ').slice(-2).join(' ')) 
                        && areAllCharsInStringAInStringB(newCurrent, input)
                    );
                if (isValid) {
                    await calc(newCurrent, arr, newIndex, length);
                }
            };
        }
    };
    await calc('', arrWord, 0, length);
    console.log('done');
    callback?.();
});

const checkExactly = (value: string, arrayValue: string[]) => {
    const word = value?.split?.(' ') ?? [];
    const length = word.length;
    switch (length) {
        case 0:
        case 1:
            return true;
        case 2:
            return arrayValue.includes(word.slice(-2).join(' '));
        default:
            return arrayValue.includes(word.slice(-2).join(' ')) || arrayValue.includes(word.slice(-3, -1).join(' '));

    }
}
