import styled from "styled-components";
import { FlexRow } from "../general/GeneralCss";
import { useAppDispatch, useAppSelector } from "../store/hooks";
import { ChangeEvent } from "react";
import { hackLiXiSlice } from "./HackLiXiSlice";

export const CheckBoxUnsign = () => {
    const dispatch = useAppDispatch();;
    const isUnsign = useAppSelector(state => state.hackLiXi.isUnsign);

    const onChangeValue = (e: ChangeEvent<HTMLInputElement>) => {
        // console.log(e.target.checked);
        dispatch(hackLiXiSlice.actions.setIsUnsign(e.target.checked));
    };

    return <Main>
        <CheckBox type="checkbox" onChange={onChangeValue} defaultChecked={isUnsign}/>
        <Title>Từ không dấu</Title>
    </Main>;   
};

const Main = styled.div`
    ${FlexRow}
    justify-content: flex-start;
    align-items: center;
`;
const CheckBox = styled.input`

`;
const Title = styled.div`

`;