import styled from "styled-components";
import { FlexRow } from "../general/GeneralCss";
import { ChangeEvent, useState } from "react";
import { useAppDispatch, useAppSelector } from "../store/hooks";
import { hackLiXiSlice } from "./HackLiXiSlice";

export const CheckBoxFindExactly = () => {
    const dispatch = useAppDispatch();;
    const isFindExactly = useAppSelector(state => state.hackLiXi.isFindExactly);

    const onChangeValue = (e: ChangeEvent<HTMLInputElement>) => {
        // console.log(e.target.checked);
        dispatch(hackLiXiSlice.actions.setIsFindExactly(e.target.checked));
    };

    return <Main>
        <CheckBox type="checkbox" onChange={onChangeValue} defaultChecked={isFindExactly}/>
        <Title>Tìm chính xác</Title>
    </Main>;    
};

const Main = styled.div`
    ${FlexRow}
    justify-content: flex-start;
    align-items: center;
`;
const CheckBox = styled.input`

`;
const Title = styled.div`

`;