export const countSpaces = (s: string): number => {
    let count = 0;
    for (let i = 0; i < s.length; i++) {
        if (s[i] === ' ') {
            count++;
        }
    }
    return count;
}

export const removeVietnameseTones = (s: string): string => {
    let r = s.toLowerCase();
    const nonAccents = ['a', 'e', 'i', 'o', 'u', 'd', 'y'];
    const accents = ['á|à|ả|ã|ạ|ă|ắ|ằ|ẳ|ẵ|ặ|â|ấ|ầ|ẩ|ẫ|ậ', 'é|è|ẻ|ẽ|ẹ|ê|ế|ề|ể|ễ|ệ', 'í|ì|ỉ|ĩ|ị', 'ó|ò|ỏ|õ|ọ|ô|ố|ồ|ổ|ỗ|ộ|ơ|ớ|ờ|ở|ỡ|ợ', 'ú|ù|ủ|ũ|ụ|ừ|ự|ư|ứ|ừ|ử|ữ|ự', 'đ', 'ý|ỳ|ỷ|ỹ|ỵ'];
    for (let i = 0; i < accents.length; i++) {
        r = r.replace(new RegExp(accents[i], 'g'), nonAccents[i]);
    }
    return r;
}