import styled from "styled-components";
import { CtaButton, FlexColumn, FlexRow, FullSizeAbsolute } from "../general/GeneralCss";
import { ResultList } from "./ResultList";
import { ChangeEvent, useEffect, useState } from "react";
import { useAppDispatch, useAppSelector } from "../store/hooks";
import { calculateInput, calculateInputInfo, inputKey } from "./HackLiXiSlice";
import { shuffleString } from "../general/Utilities";
import { Words } from "./Words";
import { CheckBoxFindExactly } from "./CheckBoxFindExactly";
import { CheckBoxUnsign } from "./CheckBoxUnsign";
import { WordLengthFilter } from "./ WordLengthFilter";

export const HackLiXi = () => {
    const dispatch = useAppDispatch();
    const [input, setInput] = useState('');
    const isFindExactly = useAppSelector(state => state.hackLiXi.isFindExactly);
    const isUnsign = useAppSelector(state => state.hackLiXi.isUnsign);
    const [isCtaReady, setIsCtaReady] = useState(true);
    const minWordLength = useAppSelector(state => state.hackLiXi.minWordLength);

    useEffect(() => {
        setInput(localStorage.getItem(inputKey) ?? shuffleString('ngũ phúc lâm môn'))
    }, []);

    const onInputChange = (e: ChangeEvent<HTMLInputElement>) => {
        setInput(e.target.value);
    }

    const onClickCta = () => {
        if (!isCtaReady) {
            calculateInputInfo.isContinue = false;
            return;
        }
        console.log('on click cta');
        setIsCtaReady(false);
        dispatch(calculateInput({
            input, isFindExactly, isUnsign, minWordLength,
            callback: () => {
                setIsCtaReady(true);
            }
        }));
    }

    return <Main>
        <Input value={input} onChange={onInputChange} />
        <CheckBoxFindExactly />
        <CheckBoxUnsign />
        <WordLengthFilter />
        <CustomCtaButton onClick={onClickCta}>{isCtaReady ? 'Tìm từ' : 'Đang tìm...'}</CustomCtaButton>
        <Words />
        <ResultList />
    </Main>;
};

const Main = styled.div`
    ${FullSizeAbsolute}
    ${FlexColumn}
    justify-content: flex-start;
    align-items: flex-start;
    gap: 10px;
    padding-top: 10px;
`;
const Input = styled.input`
    width: 100%;
    font-size: 1em;
`;
const CustomCtaButton = styled.div`
    ${CtaButton}
    padding: 5px 10px;
    font-size: 1em;
    min-width: 100px;

    ${FlexRow}
    justify-content: center;
    align-items: center;
`;


